const chokidar = require('chokidar')

export const getChokidar = () => chokidar
export let watchers = [] // eslint-disable-line

export const addWatch = (w) => { watchers.push(w) }

export const closeByIndex = (index) => {
  watchers[index].close()
  watchers = [
    ...watchers.slice(0, index),
    ...watchers.slice(index + 1),
  ]
}

export const close = () => {
  watchers.forEach((w) => { w.close() })
  watchers = []
}



